article {
	width: 1000px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	padding-bottom: 100px;
}

img {
	height: 500px;
	border-radius: 3px;
	margin: 30px 0;
	box-shadow: 0 2px 5px rgb(173, 173, 173);
}

p,
h1,
h6 {
	font-family: 'Poppins';
	width: 100%;
}

p {
	font-size: 16px;
	color: black;
	font-weight: 400;
	line-height: 1.4;
	font-family: 'Poppins';
	margin-bottom: 20px;
}

h1 {
	font-size: 30px;
	font-weight: 600;
	letter-spacing: 1px;
	margin-top: 5px;
}

h6 {
	font-size: 16px;
	margin-top: 50px;
	color: rgb(92, 92, 92);
}

@media only screen and (max-width: 1100px) {
	article {
		width: 800px;
	}
}

@media only screen and (max-width: 900px) {
	article {
		width: 600px;
	}
}

@media only screen and (max-width: 650px) {
	article {
		width: 400px;
		padding-right: 10px;
		padding-left: 10px;
	}

	h6 {
		font-size: 16px;
		margin-top: 25px;
	}

	img {
		height: auto;
		width: 98%;
		border-radius: 3px;
		margin: 30px 0;
		box-shadow: 0 2px 5px rgb(173, 173, 173);
	}
}

@media only screen and (max-width: 450px) {
	article {
		width: 100%;
		padding-right: 20px;
		padding-left: 20px;
	}

	h1 {
		font-size: 27px;
	}

	img {
		height: auto;
		width: 99%;
		border-radius: 3px;
		margin: 30px 0;
		box-shadow: 0 2px 5px rgb(173, 173, 173);
	}
}
